import React, { Component } from 'react'
import _ from 'lodash'
const accessToken = 'e6bdb60d3ec5313a17274fc3db04a4fe3d28b0fbe6b0b8305c2c84785a5ac700';
const spaceId = '52dq79v5sxg3';
import Util from '../common/Util'

export default class extends Component {
    state = { city: '' }

    static async getInitialProps({ query, req }) {
        
        const productId = _.get(query, 'id');
        const product = await Util.fetchProduct(productId);

        return {
            product
        };
    }
    // onSubmit = () => {
    //     this.setState({
    //         city: this.state.city
    //     })
    // }
    onInputChange(city) {
        this.setState({ city});
        this.props.onSearchTermChange(city);
    }
    
    render() {
        console.log(this.props.product)
        return (
            <div>
                <form>
                    <input 
                        value={this.state.city}
                        onChange={event => this.onInputChange(event.target.value)} />
                    <button type='submit'>Search</button>
                </form>
                <div>
                    <p>name: {this.props.product.stations[0].station}</p>
                    <p>fuel: {this.props.product.stations[0].diesel}</p>
                    
                </div>
                <style>
                    {`
                        div {
                            padding: 20px;
                        }
                        input {
                            width: 180px;
                        }
                        input, button {
                            height: 30px;
                            margin-right: 10px;
                        }
                    `}
                </style>
            </div>
        );
    }
}


